.button-box {
  gap: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

button {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  width: 130px;
  height: 60px;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #000;
  font-size: 18px;
}

.button-clicked {
  color: gray;
  background-color: #d3d3d3;
  border: none;
}

